<template>
  <div id="admin">
    <!--  把首页 和 后台 直接显示在这 降低了耦合性   -->
    <el-container>
      <el-header>
      </el-header>
      <el-container class="loading">
        <el-main>

        </el-main>
        <el-aside>
          <Aside
              :activeIndex="activeIndex"
              :screenWidth="screenWidth"
          />
        </el-aside>
      </el-container>
      <el-footer>
      </el-footer>
    </el-container>
  </div>
</template>
<script>
import Aside from "@/components/Aside";
import "../assets/common.css"

export default {
  name: "Admin",
  components: {
    Aside
  },
  watch: {
    // 监听屏幕宽度变化
    screenWidth: function (val) {
      this.screenWidth = val
    }
  },
  data() {
    return {
      screenWidth: document.documentElement.clientWidth,
      activeIndex: '1',
    }
  },
  methods: {
    handleScreen() {
      this.screenWidth = document.documentElement.clientWidth;
    },
    mounted() {
      window.addEventListener('resize', this.handleScreen)
    }
  }
}
</script>

